@import url(https://allfont.ru/allfont.css?fonts=avanti);

.container {
  max-width: 1200px;
  margin: 0 auto;
}
body{
  font-family: 'Avanti',
  arial;
}

h1{
  color: #000;
  font-size: 52px;
  font-weight: 900;
}
p{
  color: #727272;
  font-size: 14px;
}
img{
  max-width: 100%;
}
ul{
  margin: 0;
  padding: 0;
}


/*==============================
=            HEADER            =
==============================*/

.header {
  padding-bottom: 10px;
}
.header__wrapper-up-bg{
  padding-top: 10px;
  padding-bottom: 10px; 
  background-color: rgb(238, 238, 238);
}
.header__wrapper-up {
  @include flex-all-sb;
}

.social__block {
  a{
    cursor: pointer;
    text-decoration: none;
  }
}

.search {
  position: relative;
  max-width: 100%;
  cursor: pointer !important;  
  margin-right: 20px;
  a{
    text-decoration: none;
    cursor: pointer;
  }
  .search__input{
    position: absolute;
    z-index: 999;
    opacity: 1;
    top: -5px;
    left: -305px;
    display: none;
    input{
      border-radius: 5px;
      padding-left: 10px;
      outline: none;
      border: 1px solid rgb(238, 238, 238);
      height: 35px;
      width: 290px;
    }
  }
}

.header__menu {
  display: flex;
  align-items: center;
  ul{
    padding: 0;
    list-style-type: none;
    @include flex-all-sb;
    li{
      a{
        text-decoration: none;
        color: #000;
      }
      position: relative;
      margin-left: 15px;
      margin-right: 15px;
      &:before{
        content: '';
        position: absolute;
        left: -15px;
        width: 2px;
        height: 20px;
        background-color: #ccc;
      }
      &:nth-child(2):after{
        content: '';
        position: absolute;
        right: -15px;
        width: 2px;
        height: 20px;
        background-color: #ccc;
      }
    }
  }
  
}





.header__wrapper-down {
  width: 100%;
  @include flex-all-sb;
  padding-top: 20px;
  padding-bottom: 20px;
}

.nav {
  ul{
    list-style-type: none;
  }
  .nav__list {
    position: relative;
    @include flex-all-sb;
    list-style-type: none;
    padding: 0;
    
    li{
      a{
        color: #000;
        font-size: 16px;
        text-decoration: none;
      }
      margin-left: 15px;
      margin-right: 15px;
      &:nth-child(1){
        a{
          font-weight: normal;
          color: #fff; 
        }
        border-radius: 5px;
        background-color: #373d95;
        padding: 25px 70px;
      }
    }

    .list__down{
      position: relative;
      .list {
        padding: 0;
        margin: 0;
        li{
          background: none;
          margin-bottom: 10px;
          margin-left: 0;
          padding: 0;
          a{
            color: #000;
          }
        }
        display: block;

        position: absolute;
        background-color: #fff;
        border-radius: 10px;
        padding-top: 20px;
        padding-left: 5px;
        padding-bottom: 5px;
        width: 150px;
        z-index: -999;
        opacity: 0;
        transform: translatey(-20px);
        transition: all .2s ease;
      }
      &:hover .list{
        z-index: 999;
        opacity: 1;
        transform: translatey(0);
      }
    }
    
  }
}

.phone {
  cursor: pointer;
  border: 2px solid rgb(238, 238, 238);
  border-radius: 7px;
  padding: 15px 30px;
  text-align: center;
  a{
    text-decoration: none;
    color: #373d95;
    font-weight: 900;
    font-size: 18px;
    margin-bottom: 15px;
  }
}
.dropdown__btn{
  position: relative;
  .nav__btn {
    position: relative;
    &::before{
      content: url('../img/svg/menu.svg');
      position: absolute;
      
      left: -40px;
      top: -1px;
    }
    &::after {
      content: url('../img/svg/down.svg');
      position: absolute;
    
      right: -40px;
      top: 2px;
    }
  }
}
.dropdown__menu{
  position: absolute;
  width: 200px;
  margin: 0;
  padding: 10px;
  top: 80px;
  left: 12px;
  z-index: 999;
  border: 2px solid rgb(238, 238, 238);
  background-color: #fff;
  border-radius: 5px;
  display: none;
  div{
    margin-bottom: 10px;
  }
  a{
    text-decoration: none;
    color: #000;
    font-size: 14px;
    font-weight: 700;
    transition: .2s;
    &:hover{
      color: rgb(141, 141, 141);
    }
  }
}



.advert__wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin: 0 auto;
  max-width: 900px;
  padding-top: 70px;
  padding-bottom: 70px;
}

.advert {
  .advert__content{
    text-align: left;
    max-width: 700px;
    margin-bottom: 30px;
  }

    p{
      &:nth-child(1){
        font-size: 56px;
        color: #727272;
      }
      &:nth-child(2) {
        font-size: 56px;
        font-weight: 700;
        margin-bottom: 10px;
      }
      &:nth-child(3) {
        font-size: 16px;
        color: #727272;
      }
      &:nth-child(4) {
        font-size: 16px;
        color: #727272;
      }

    }

}
.btn__blue{
  display: inline-block;
  background-color: #373d95;
  padding-top: 13px;
  padding-bottom: 13px;
  transition: .2s;
  cursor: pointer;
  border-radius: 5px;
  a{
    padding: 15px 20px;
    text-decoration: none;
    color: #fff;
    font-weight: 700;
    font-size: 16px;
  }
  &:hover {
    background-color: #000;
  }
}

.card{
  padding-top: 70px;
  padding-bottom: 70px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
  img{
    max-width: 100%;
  }
}


.product__card{
  @include flex-all-sb;
  flex-wrap: wrap;
}

.card__item{
  max-width: 250px;
  img{
    padding: 0;
    margin: 0;
    max-width: 100%;
  }
  text-align: center;
  .card__btn__block{
    width: 100%;
    margin: 0 auto;
    @include flex-all-center;
    .card__btn{
      display: inline-block;
      margin-top: 10px;
      cursor: pointer;
      border: 2px solid rgb(238, 238, 238);
      border-radius: 7px;
      text-align: center;
      padding-top: 5px;
      padding-bottom: 5px;
      transition: .5s;
      &:hover {
        border: 2px solid #373d95;
        background-color: #373d95;
        a{
          color: #fff;
        }
      }
    a {
      text-decoration: none;
      color: #373d95;
      font-weight: 900;
      font-size: 14px;
      margin-bottom: 15px;
      padding: 5px 30px;
    }
    }
  }
  p{
    color: #000;
  }
}

.question{
  padding-top: 100px;
  padding-bottom: 50px;
}

.question__wrapper{
  display: flex;
  justify-content: space-between;
}

.question__content{
  h1{
    margin-bottom: 30px;
  }
  width: 41%;
}

.question__form{
  width: 50%;
  .form__input__wrapper{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2,1fr);
    grid-gap: .5rem;
    margin-bottom: 20px;
    .form__input{
      margin: 0!important;
    }
  }
}


.about{
  padding-top: 100px;
  padding-bottom: 100px;
}
.about__wrapper{
  display: flex;
  justify-content: space-between;
}

.about__content{
  h1{
    margin-bottom: 30px;
  }
  width: 35%;
}
.about__form{
  margin-left: 50px;
}

.about__info{
  margin-top: 30px;
  div{
    margin-bottom: 35px;
  }
  .info__title{
    font-weight: 400;
    font-size: 15px;
  }
  .info__text{
    font-weight: 700;
    color: #000;
    margin-top: 10px;
  }
}


.map__wrapper {
  padding-bottom: 100px;
}

.map {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 1rem;
}

.map__item {
  background-color: #000;
  width: 100%;
  max-height: 400px;
}
.map__item_1{
  overflow: hidden;
  display: flex;
  .map__img{
    width: 100%;
    object-fit: cover;
    object-position: center;
  }
}




.gallery__wrapper {
  margin-bottom: 50px;
}

.grid__gallery {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 200px;
  grid-gap: .5rem;
  .grid__item-1 {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 3;
  }
  .grid__item-2 {
    grid-column-start: 2;
    grid-row-start: 1;
    grid-row-end: 2;
  }
  .grid__item-3 {
    grid-column-start: 2;
    grid-row-start: 2;
    grid-row-end: 3;
  }
  .grid__item-4 {
    grid-column-start: 3;
    grid-row-start: 1;
    grid-row-end: 2;
  }
  .grid__item-7 {
    grid-column-start: 3;
    grid-row-start: 2;
    grid-row-end: 4;
  }

}

.grid__item {
  overflow: hidden;
  a{
    position: relative;
    img{
      width: 100%;
      object-fit: contain;
    }
    .item__text{
      display: none !important;
      position: absolute;
      z-index: 999;
      display: flex;
      left: 20px;
      bottom: 50px;
      color: #fff;
    }
  }
  &:hover{
    a{
      .item__text{
        display: block !important;
      }
      &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 450px;
        background-color: rgba(0, 0, 0, .5);
        z-index: 999;
      }
    }
  }
}

/*==============================
=            FOOTER            =
==============================*/

.footer {
  border-top: 1px solid #727272;
  padding-top: 50px;
  background-color: #fff;
}

.footer__wrapper {
  display: flex;
}

.footer__content {
  width: 45%;
  p{
    color: #000 !important;
  }
  .footer__text{
    font-size: 14px;
    margin-bottom: 30px;
  }
}

.logo {
  margin-bottom: 20px;
  max-width: 200px;
  img{
    width: 100%;
  }
}

.footer__info {
  .info__block{
    display: flex;
    margin-bottom: 10px;
  }
}


.info__title {
  font-size: 14px !important;
  font-weight: 700;
  color: #fff !important;
  margin-right: 3px;
}

.info__text {
  font-size: 14px !important;
}

.footer__social {
  display: flex;
}

.footer__lists {
  width: 40%;
  margin-left: 50px;
  display: flex;
  justify-content: space-between;
}

.lists__item {}

.title {
  font-weight: 700;
  margin-bottom: 20px;
}

.list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  li{
    margin-bottom: 10px;
    a{
      text-decoration: none;
      color: #000;
      transition: .2s;
      &:hover{
        color: #373d95;
      }
    }
  }
}


.footer__form{
  margin-top: 40px;
  border-top: 1px solid #000;
  padding-top: 30px;
  padding-bottom: 50px;
  background-color: #383838;
}
.footer__form-wrapper{
  display: flex;
  align-items: center;
  justify-content: space-between;
  .link{
    a{
      text-align: center;
      font-size: 18px;
    color: #727272;
    }
    
  }
  .form{
    display: flex;


    input{
      margin: 0 !important;
      width: 500px;
      height: 50px;
      background-color: #383838;
      border: 1px solid #727272;
      border-radius: 10px 0 0 10px ;
      outline: none;
      padding-left: 10px;

    }
    button{
      width: 150px;
      margin-top: 0;
      cursor: pointer;
      background-color: #373d95;
      border: none;
      color: #fff;
      padding-left: 20px;
      padding-right: 20px;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: .1em;
      margin-left: -10px;
      border-radius: 0 10px 10px 0;
      transition: .2s;
      &:hover{
        background-color: #595fb1;
      }
    }
  }
}


/*==============================
=            SLIDER            =
==============================*/

.mySwiper {
  background-color: rgb(238, 238, 238);
  width: 100%;
  height: 100%;
}

.swiper-wrapper{
}
.swiper-slide {
  font-size: 18px;
}

.swiper-button-next{
  &::after{
    content: url("../img/svg/down2.svg") !important;
    margin-right: 100px !important;
    padding-right: 19px;
    padding-bottom: 14px;
    padding-top: 0;
    padding-left: 20px;
    transform: rotate(-90deg);
    background-color: #fff;border-radius: 7px;
    left: -10px;
  }
}
.swiper-button-prev {
  &::after {
    content: url("../img/svg/down2.svg") !important;margin-left: 100px !important;
    border-radius: 7px;
    transform: rotate(90deg);
    padding-right: 19px;
    padding-bottom: 14px;
    padding-top: 0;
    padding-left: 20px;
    background-color: #fff;
  }
}

/*==============================
=            POP-UP            =
==============================*/
.popup-fade{
  background-color: rgb(0, 0, 0);
}
.popup {
  position: fixed;
  top: 20%;
  left: 50%;
  padding: 20px;
  width: 360px;
  margin-left: -200px;
  background: #fff;
  border-radius: 4px;
  z-index: 99999;
  display: none;
}


.form{
  button{
    width: 100%;
    margin-top: 20px;
    color: #fff;
    background-color: #373d95;
    transition: .2s;
    border: none;
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 7px;
    &:hover{
      background-color: #595fb1;
    }
  }
  textarea{
    resize: vertical;
    outline: none;
    margin-bottom: 10px;
    width: 100%;
    height: 100px;
    border: 1px solid #9c9c9c;
    border-radius: 7px;
    padding: 10px;
  }
  .form__input{
    outline: none;
    margin-bottom: 10px;
    width: 100%;
    height: 30px;
    border: 1px  solid #9c9c9c;
    border-radius: 7px;
    padding-left: 10px;
    &:nth-child(1){
      margin-top: 20px;
    }
  }
}

.popup-close {
  position: absolute;
  top: 10px;
  right: 10px;
}



/*==============================
=            TABS            =
==============================*/
.tab{
  height: auto;
}
.tab__list{
  position: relative;
  max-width: 700px;
  margin: 0 auto;
  list-style-type: none;
  @include flex-all-sb
  
  
}
.active{
  position: relative;
  background-color: #373d95;
  a{
    color: #fff !important;
  }
  &::before{
    content: '';
    position: absolute;
    width: 100%;
    top: 70px;
    left: 0;
    height: 2px;
    background-color: #373d95;
  }
}
.tabs__content {
  border-top: 1px solid rgb(224, 215, 215);
  margin-top: 20px;
  position: relative;
  .tab-item{
    width: 100%;
    display: none;
  }
}
.tabs__active{
  display: block !important;
}
.btn__white{
  cursor: pointer;
  border: 1px solid #373d95;
  border-radius: 5px;
  padding-top: 15px;
  padding-bottom: 16px;
  a{
    padding: 17px 50px;
    text-transform: uppercase;  
    font-weight: 700;
    font-size: 17px;
    text-decoration: none;
    color: #000;
  }
}
.tab-item{
  max-width: 100%;
}


@media (max-width:1250px) {

  .container {
    max-width: 100%;
    margin-left: 30px;
    margin-right: 30px;
  }
  
  .header {
    display: none;
  }
  
  .header__mobile {
    display: block !important;
  
    .header__wrapper-up {
      @include flex-all-sb;
    }
  }
  .header__wrapper-down {
    display: block;

    .nav__list {
      display: block;
    }
  }


  .card {
    text-align: center;
    display: block;
  }

  input {
    width: 100% !important;
  }

  .about {
    padding-top: 0;
  }

  .about__wrapper {
    display: block;
  }

  .about__content {
    width: 100%;
  }

  .about__form {
    text-align: center;
    width: 100%;
    margin-left: 0;
  }

  .footer__lists {
    display: block;
  }

  .tab__list {
    display: block;

    .active{
      &::before{
        content: none;
      }
    }
    
  }
  .product__card {
    justify-content: center !important;
  }

  .question {
    padding-top: 0;
  }

  .question__wrapper {
    flex-wrap: wrap;
    justify-content: center;
  }

  .question__content {
    flex: 0 0 100%;
  }

  .question__form {
    width: 100%;
  }

  .footer__form-wrapper {
    display: block;

    .link {
      margin-bottom: 30px;
    }
  }

  .footer__content {
    width: 100%;
    margin-bottom: 30px;
  }

  .lists__item {
    &:nth-child(1) {
      margin-bottom: 40px;
    }
  }
  

  .btn {
    top: 0;
    right: 0;
    height: 35px;
    width: 30px;
    padding: 5px;
    background: #727272;
    position: absolute;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
  }
  
  .btn .bar1,
  .btn .bar2,
  .btn .bar3 {
    height: 2px;
    width: 100%;
    display: block;
    background: #fff;
  }
  
  .close-btn {
    z-index: 9999;
    top: 0;
    right: 0;
    height: 35px;
    width: 30px;
    padding: 5px;
    background: #727272;
    position: absolute;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
  }
  
  .close-btn .bar1 {
    transform: rotate(-130deg);
    height: 3px;
    width: 90%;
    display: block;
    background: #fff;
    position: absolute;
  }
  
  .close-btn .bar2 {
    display: none;
  }
  
  .close-btn .bar3 {
    transform: rotateZ(130deg);
    height: 3px;
    width: 90%;
    display: block;
    background: #fff;
    position: absolute;
  }
  
  /* ------------- Hide menu ----------- */
  .menu {
    display: none;
  }
  
  /* ------------------- Menu styling --------------- */
  .open-menu {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    z-index: 999;
    background: #727272;
    transition: 5s all;
  }
  
  
  .header__wrapper-up {
    justify-content: flex-end !important;
  
    .social__block {
      margin-right: 30px;
    }
  }
  .header__text {
    margin-right: 100px;
  }

  .map{
    grid-template-columns: auto;
  }

  .grid__gallery{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    
  }

  .swiper-button-next {
    &::after {
      margin-right: 50px !important;
    }
  }
  
  .swiper-button-prev {
    &::after {
      margin-left: 50px !important;
    }
  }
  
}

@media (max-width:768px) {
  
  .grid__gallery {
    display: block;
  
  }
  .dropdown__btn {
    .nav__btn {
      display: flex;
      justify-content: center;
    }

    margin-bottom: 30px;
  }

  .dropdown__menu {
    margin-top: 10px;
    width: 92%;
    left: 15px;
  }

  .nav__list {
    .nav__list-item {
      margin-bottom: 15px;
    }

    .nav__link {
      font-weight: 700;
      font-size: 24px !important;
    }
  }

  .phone {

    margin: 0 auto;
    width: 90%;
    margin-top: 40px;
  }


  .footer__wrapper {
    display: block;
  }

  .footer__lists {
    margin: 0;
  }

  
  .header__text {
    display: none;
  }

  .swiper-button-next {
    &::after {
      margin-right: 10px !important;
    }
  }
  
  .swiper-button-prev {
    &::after {
      margin-left: 10px !important;
    }
  }
}

